<template>
  <div>
    <b-button v-if="!fetched" @click="getShopItem">取得清單</b-button>
    <div v-if="items.length === 0">
      <h5 v-if="manage">沒有商品記錄，請新增一項商品</h5>
      <h5 v-else>這間店空空的，叫你老闆拿東西出來賣</h5>
    </div>
    <div v-else>
      <b-table responsive :items="items" :fields="fields">
        <template slot="specials" slot-scope="row">
          <b-list-group-item v-for="(item, index) in row.unformatted" :key="index">
            <p class="name">{{item.name}}<span class="name-tag">$ {{item.price}}</span></p>
          </b-list-group-item>
        </template>
        <template slot="actions" slot-scope="row" v-if="manage">
          <b-button size="sm" @click.stop="row.toggleDetails">
            {{ row.detailsShowing ? '關閉' : '修改' }}
          </b-button>
        </template>
        <template slot="name" slot-scope="row">
          <p class="name">{{row.item.name}}</p>
        </template>
        <template slot="row-details" slot-scope="row" v-if="manage">
          <b-card bg-variant="secondary">
            <b-input-group prepend="名稱">
              <input class="form-control" v-model="row.item.name"/>
            </b-input-group>
            <b-input-group prepend="價格">
              <input class="form-control" v-model="row.item.price" min="0" type="number"/>
            </b-input-group>
            <div v-for="(special, index) in row.item.specials" :key="index">
              <b-input-group :prepend="`客製${index + 1}名稱`">
                <input class="form-control" v-model="special.name" type="text"/>
              </b-input-group>
              <b-input-group :prepend="`客製${index + 1}價格`">
                <input class="form-control" v-model="special.price" type="number" min="0"/>
              </b-input-group>
            </div>
            <b-button @click="updateItem(row.item)" :disabled="ifDisabled(row.item) === true" :variant="ifDisabled(row.item) ? '' : 'success'">更新商品</b-button>
            <b-button @click.stop="deleteItem(row.item, row.index)" variant="danger">刪除商品</b-button>
          </b-card>
        </template>
      </b-table>
    </div>
    <b-card-group v-if="manage">
      <b-card>
        <b-input-group v-for="(key, index) in Object.keys(new_item)" :key="index" :prepend="new_item[key].prepend">
          <input class="form-control" :type="new_item[key].inputType" min="0" v-model="new_item[key].value"/>
        </b-input-group>
        <b-button @click="addItem(new_item), flipCustom=true" :disabled="ifDisabled(new_item, 'value') === true" :variant="ifDisabled(new_item, 'value') ? '' : 'success'">新增商品</b-button>
      </b-card>
      <b-card>
        <item-form @set-item-result="specials = $event" :item-list="specials"></item-form>
      </b-card>
    </b-card-group>
  </div>
</template>
<script>
import {mapState, mapActions} from 'vuex'
import ItemForm from '@/components/ItemForm'

export default {
  name: 'ItemTable',
  components: { ItemForm },
  data () {
    return {
      fields: {
        name: { label: '名稱' },
        specials: { label: '客製' },
        price: { label: '價格' },
        actions: { label: '修改' },
      },
      new_item: {
        name: {inputType: 'text', prepend: '名稱', value: null},
        price: {inputType: 'number', prepend: '價錢', value: null},
      },
      specials: [],
    }
  },
  mounted () {
    this.init()
  },
  props:{
    manage: false
  },
  computed: {
    ...mapState([
      'items',
      'userInformation'
    ]),
    fetched: function() {
      if(this.items){
        return true
      } else {
        return false
      }
    },
  },
  methods: {
    ...mapActions(['fbGet', 'fbPost', 'fbUpdate', 'fbDelete']),
    init () {
      this.getShopItem();
    },
    addItem: function(item) {
      this.fbPost([`shops/${this.userInformation.shopId}/items`, {
        name: item.name.value,
        price: typeof item.price.value === 'string' ? parseInt(item.price.value, 10) : item.price.value,
        specials: this.specials,
      }]).then((res)=>{
        if (res.id) {
          Object.keys(this.new_item).forEach(element => {
            this.new_item[element].value = null
            this.specials = []
            })
          this.getShopItem()
        }
      })
    },
    getShopItem: function() {
      this.fbGet([`shops/${this.userInformation.shopId}/items`, 'items'])
    },
    deleteItem: function(item, index) {
      this.fbDelete([`shops/${this.userInformation.shopId}/items/${item.id}`])
      .then(() => {
        this.items.splice(index, 1)
        delete this.$store.state.items[item.name]
      })
    },
    updateItem: function(item) {
      item.specials.forEach(e => e.price = parseInt(e.price, 10))
      this.fbUpdate([`shops/${this.userInformation.shopId}/items/${item.id}`, {
        name: item.name,
        price: item.price,
        specials: item.specials
      }])
    },
    ifDisabled: function(item, prop) {
      let keys = Object.keys(item)
      let disabled = false
      for(let i = 0; i < keys.length; i += 1){
        if(prop){
          if(!item[keys[i]][prop]){ disabled = true }
        } else {
          if(!item[keys[i]]){ disabled = true }
        }
      }
      return disabled
    },
  }
}
</script>
<style lang="stylus" scoped>
p
  white-space nowrap
.name
  font-size 1em
.name-tag
  color red
  border 1px solid black
</style>


