<template>
  <div>
    <b-alert show>
      <p>
        客製清單<span v-if="area_show === false">( {{itemList.length}} 項)</span>
      </p>
      <b-button @click="area_show = !area_show">{{ area_show ? '收合': '展開' }}</b-button>
    </b-alert>
    <div v-if="area_show === true">
      <div v-if="itemList.length > 0">
        <b-table :items="itemList" :fields="itemList_fields">
          <template slot="action" slot-scope="row">
            <b-button size="sm" @click.stop="removeFromList(row.item)" class="mr-1">
              刪除
            </b-button>
          </template>
        </b-table>
      </div>
      <div v-else>
        <p>沒有客製項目</p>
      </div>
      <div>
        <b-card no-body>
          <b-alert variant="danger" dismissible :show="exist_alert" @dismissed="exist_alert = false">已在清單上</b-alert>
        </b-card>
        <b-input-group v-for="(key, index) in Object.keys(new_item)" :key="index" :prepend="new_item[key].prepend">
          <input class="form-control" :type="new_item[key].inputType" min="0" v-model="new_item[key].value"/>
        </b-input-group>
        <b-button :disabled="ifDisabled(new_item, 'value')" variant="success" @click="addToList()">確定</b-button>
        <b-button @click="clearInput()">重填</b-button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ItemForm',
  props: {
    itemList: {
      type: Array,
      default: function () {
        return []
      }
    }
  },
  data () {
    return {
      collections: [],
      area_show: false,
      exist_alert: false,

      itemList_fields: [
        { key: 'name', label: '名稱'},
        { key: 'price', label: '價格' },
        { key: 'action', label: ''}
      ],
      new_item: {
        name: {inputType: 'text', prepend: '名稱', value: null, special_temp: null, special: []},
        price: {inputType: 'number', prepend: '價錢', value: null, special_temp: null, special: []},
      },
    }
  },
  methods:{
    addToList: function() {
      if(this.new_item.name.value !== ''){
        let e = this.itemList.findIndex(item => item.name === this.new_item.name.value)
        if(e === -1){
          this.exist_alert = false
          this.itemList.push(Object.assign({}, {
            name: this.new_item.name.value,
            price: this.new_item.price.value
          }))
        } else {
          this.exist_alert = true
        }
      }
      this.$emit('set-item-result', this.itemList)
      this.clearInput()
    },
    clearInput: function() {
      this.new_item.name.value = null
      this.new_item.price.value = null
    },
    removeFromList: function(e) {
      let index = this.itemList.findIndex(item => item.name === e.name)
      if(index !== -1){
        this.itemList.splice(index, 1)
      }
    },
    ifDisabled: function(item, prop) {
      let keys = Object.keys(item)
      let disabled = false
      for(let i = 0; i < keys.length; i += 1){
        if(prop){
          if(!item[keys[i]][prop]){ disabled = true }
        } else {
          if(!item[keys[i]]){ disabled = true }
        }
      }
      return disabled
    },
  }
}
</script>


